




































import { Component, Vue, Provide } from "vue-property-decorator";
import { Form as ElForm, Input } from 'element-ui'
import { UserModule } from '@/store/modules/user'
@Component({
  components: {}
})
export default class Login extends Vue {
  @Provide() isLoading: boolean = false;

  private ruleForm = {
    username: 'lativsudamin',
    password: 'aaaa1234',
    KeepLogin: true,
  }

  private rules = {
    username: [{ required: true, message: "管理帳號不能為空", trigger: "blur" }],
    password: [{ required: true, message: "密碼不能為空", trigger: "blur" }]
  };
  ToFindPWPage(): void {
    this.$emit("change-findpw");
  }
  HandleLogin(): void {
    (this.$refs.ruleForm as ElForm).validate( async(isValidate:boolean)=>{
      if(isValidate){
        this.isLoading = true
        const status = await UserModule.Login(this.ruleForm)
        if(status){
          this.$router.push('/')
        }
        setTimeout(()=>{
          this.isLoading = false
        },500)
      }
    })
  }
}
