import { VuexModule, Module, Action, Mutation, getModule } from 'vuex-module-decorators'
import { login, logout, getUserInfo } from '@/api/user'
import Vue from 'vue'
import { getToken, setToken, removeToken } from '@/utils/cookies'
// import { getLS_Token , setLS_Token , removeLS_Token } from '@/utils/localstorage'
import JWT_DECODE from 'jwt-decode'
import store from '@/store'
import { resetRouter } from '@/router'

export interface IUserState {
  token: string
  name: string
  avatar: string
  introduction: string
  roles: string[]
  email: string
}

@Module({ dynamic: true, store, name: 'user' })
class User extends VuexModule implements IUserState {
  public token = getToken() || ''
  public name = ''
  public avatar = ''
  public introduction = ''
  public roles: string[] = []
  public email = ''

  @Mutation
  private SET_TOKEN(token: string) {
    this.token = token
  }

  @Mutation
  private SET_NAME(name: string) {
    this.name = name
  }

  @Mutation
  private SET_AVATAR(avatar: string) {
    this.avatar = avatar
  }

  @Mutation
  private SET_INTRODUCTION(introduction: string) {
    this.introduction = introduction
  }

  @Mutation
  private SET_ROLES(roles: string[]) {
    this.roles = roles
  }

  @Mutation
  private SET_EMAIL(email: string) {
    this.email = email
  }

  @Action
  public async Login(userInfo: { username: string, password: string}) {
    let { username, password } = userInfo
    username = username.trim()
    let status:boolean = false
    const isLogin = login({ username, password })
    await isLogin
    .then((res:any)=>{
      if(res.code != 20000){
        status = false
        return 
      }
      Vue.ls.set('Access-Token',res.token,1000 * 60 * 24 * 7)
      setToken(res.token)
      this.SET_TOKEN(res.token)
      status = true
      return
    })
    .catch(err=>{
      status = false
      return
    })
    return status
  }

  @Action
  public ResetToken() {
    removeToken()
    Vue.ls.remove('Access-Token')
    this.SET_TOKEN('')
    this.SET_ROLES([])
  }

  @Action
  public async GetUserInfo() {
    if (this.token === '') {
      throw Error('GetUserInfo: token is undefined!')
    }
    // const { data } = await getUserInfo({ /* Your params here */ })
    // if (!data) {
    //   throw Error('Verification failed, please Login again.')
    // }
    const data = JWT_DECODE(this.token)
    
    const { roles, name, avatar, introduction, email } = data as any
    // roles must be a non-empty array
    if (!roles || roles.length <= 0) {
      throw Error('GetUserInfo: roles must be a non-null array!')
    }
    this.SET_ROLES(JSON.parse(roles))
    this.SET_NAME(name)
    this.SET_AVATAR(avatar)
    this.SET_INTRODUCTION(introduction)
    this.SET_EMAIL(email)
  }
  @Action
  public async LogOut(){
    Vue.ls.remove('Access-Token')
    removeToken()
    resetRouter()
    this.SET_TOKEN('')
    this.SET_ROLES([])
  }
}


export const UserModule = getModule(User)
